<template>
  <el-container>
    <!-- header -->
    <el-header style="height: 15%;">
      <el-col :span="8" class="login_banner">
        <el-image :src="image.logo"></el-image>
      </el-col>
    </el-header>
    <!--  -->
    <el-main>
      <el-col :span="15" class="login_banner">
        <el-image :src="image.login_banner"></el-image>
      </el-col>
      <el-col :span="1" style="margin-top: 6em; text-align: center;">
        <div class="banner-text">
          <span></span>
        </div>
      </el-col>
      <el-col :span="7" class="login_banner">
        <div class="login_container">
          <div class="subs">
            <div class="login-text">&nbsp;&nbsp;&nbsp;</div>
            <el-divider></el-divider>
            <!-- ---------------- -->
            <!--  v-if   v-if DOM -->
            <div v-show="show">
              <div id="login"></div>
            </div>
            <!--  -->
            <div v-if="disappear" class="subs-subs">
              <el-form :model="form" :rules="rules" ref="from">
                <el-form-item prop="username">
                  <el-input
                    v-model="form.username"
                    placeholder=""
                    suffix-icon="el-icon-user"
                  ></el-input>
                </el-form-item>
                <el-form-item prop="password">
                  <el-input
                    v-model="form.password"
                    type="password"
                    placeholder=""
                    suffix-icon="el-icon-lock"
                  ></el-input>
                </el-form-item>
                <span class="errmsg">{{ msg }}</span>
                <el-form-item class="keep-alive">
                  <el-checkbox v-model="keep" true-label="true"></el-checkbox>
                </el-form-item>
                <el-form-item>
                  <el-button type="danger" style="width: 18em;" @click="submit">&nbsp;</el-button>
                </el-form-item>
              </el-form>
            </div>

            <el-divider></el-divider>
            <div>
              <el-row>
                <el-col :span="4">
                  <el-link @click="qq_login" :underline="false" type="primary">QQ</el-link>
                </el-col>
                <el-col :span="6">
                  <el-link @click="dt_login" :underline="false" type="primary">{{ type }}</el-link>
                </el-col>
                <el-col :span="4">
                  <el-link @click="$router.push({name: 'home'})" :underline="false" type="info"></el-link>
                </el-col>
                <el-col :span="10">
                  <!-- clickrouter -->
                  <el-link
                    :underline="false"
                    type="danger"
                    @click="$router.push({ name: 'register', query:{redirect: $route.query.redirect} })"
                  >
                    
                    <i class="el-icon-d-arrow-right el-icon--right"></i>
                  </el-link>
                </el-col>
              </el-row>
            </div>
          </div>
        </div>
      </el-col>
    </el-main>
    <!-- footer -->
    <Footer style="height: 15%;"></Footer>
  </el-container>
</template>

<script>
/* eslint-disable */ //------jseslint
import Footer from "@/components/Footer.vue";

export default {
  name: "Login",
  data() {
    return {
      // 
      image: {
        logo: require("../assets/images/logo02.png"),
        login_banner: require("../assets/images/login_banner.png")
      },

      // 
      form: {
        username: "",
        password: ""
      },
      // 
      rules: {
        username: [
          {
            required: true,
            message: "  ",
            trigger: "blur"
          }
        ],
        password: [
          {
            required: true,
            message: "  ",
            trigger: "blur"
          }
        ]
      },

      // 
      keep: "",

      //
      msg: "",

      //
      show: false,
      disappear: true,
      type: "",

      // 
      redirect: ""
    };
  },
  mounted() {},
  methods: {
    // 
    submit() {
      this.$refs.from.validate(valid => {
        if (valid) {
          let that = this;

          that.$axios
            .post("login/", that.form)
            .then(res => {
              // token
              sessionStorage.clear(); // sessionStorage
              localStorage.clear(); // 

              if (that.keep === "true") {
                localStorage.token = res.data.token;
                localStorage.username = res.data.username;
                localStorage.user_id = res.data.id;
              } else {
                sessionStorage.token = res.data.token;
                sessionStorage.username = res.data.username;
                sessionStorage.user_id = res.data.id;
              }

              that.$store.commit("setStatus", {
                username: res.data.username,
                token: res.data.token
              });
              // 
              that.$router.replace(that.$route.query.redirect || "/");
            })
            .catch(err => {
              if (err.non_field_errors) {
                that.msg = "";
              }
            });
        }
      });
    },

    // QQ
    qq_login() {
      // 
      // let next = this.$route.params.path || "/";
      let that = this;
      this.$axios
        .get("/oauth/qq/authorization/", {
          params: { next: this.$store.state.path }
        })
        .then(res => {
          location.href = res.data.login_url;
        })
        .catch(err => {
          that.$message({
            type: "error",
            message: "QQ",
            center: true
          });
        });
    },

    // url
    dt_login() {
      // 
      // let next = this.$route.params.path || "/";
      let that = this;

      if (that.show) {
        that.show = false;
        that.disappear = true;
        that.type = "";
      } else {
        that.show = true;
        that.disappear = false;
        that.type = "";

        that.$axios
          .get("/oauth/dt/authorization/")
          .then(res => {
            // 
            var obj = DDLogin({
              id: "login", //HTMLid<div id="login_container"></div><span id="login_container"></span>
              goto: encodeURIComponent(res.data.url), //
              style: "border:none;background-color:#FFFFFF;",
              width: "250",
              height: "300"
            });

            // 
            var handleMessage = function(event) {
              var origin = event.origin;
              if (origin == "https://login.dingtalk.com") {
                //ddLogin
                var loginTmpCode = event.data;
                //loginTmpCode
                // console.log("loginTmpCode", loginTmpCode);
                that.$axios
                  .get("/oauth/dt/authorization/", {
                    params: { loginTmpCode: loginTmpCode }
                  })
                  .then(res => {
                    location.href = res.data.url;
                  })
                  .catch(err => {
                    that.$message({
                      type: "error",
                      message: "",
                      center: true
                    });
                  });
              }
            };

            // 
            if (typeof window.addEventListener != "undefined") {
              window.addEventListener("message", handleMessage, false);
            } else if (typeof window.attachEvent != "undefined") {
              window.attachEvent("onmessage", handleMessage);
            }
          })
      }
    }
  },
  components: {
    Footer
  }
};
</script>

