<template>
  <Container>
    <el-divider></el-divider>
    <el-row>
      <el-col :span="2">
        <pre style="margin:0; padding: 0;">&nbsp;</pre>
      </el-col>
      <el-col :span="20">
        <div class="title">
          <p style="font-size: 1.2em; color: red;">
            <span class="el-icon-shopping-cart-full"></span>
          </p>
        </div>
      </el-col>
    </el-row>
    <!--  -->
    <el-row>
      <el-col :span="2">
        <pre style="margin:0; padding: 0;">&nbsp;</pre>
      </el-col>
      <el-col :span="20">
        <div class="detail">
          <div>
            <!--  -->
            <el-table
              :data="skus"
              size="mini"
              height="40em"
              highlight-cuurent-row
              ref="skuList"
              :header-cell-style="{background:'#eef1f6',color:'#606266'}"
              :header-row-style="{height:'5em'}"
              @selection-change="handleSelectionChange"
              @row-click="handleRowClick"
            >
              <el-table-column type="selection" align="center"></el-table-column>
              <el-table-column width="150" align="center">
                <template slot-scope="scope">
                  <el-image :src="scope.row.default_image_url" style="width: 8em; height: 8em;"></el-image>
                </template>
              </el-table-column>
              <el-table-column prop="name" label="" width="300" align="center"></el-table-column>
              <el-table-column prop="spec" label="" width="150" align="center"></el-table-column>
              <el-table-column prop="price" label="" width="100" align="center"></el-table-column>
              <el-table-column label="" width="180" align="center">
                <template slot-scope="scope">
                  <el-input-number
                    v-model="scope.row.count"
                    :min="1"
                    :max="scope.row.stock"
                    size="mini"
                    @change="edit_cart(scope.row)"
                  ></el-input-number>
                </template>
              </el-table-column>
              <el-table-column label="" width="120" align="center">
                <!--  -->
                <template
                  slot-scope="scope"
                >{{ (scope.row.count * scope.row.price).toFixed(2) }} &nbsp;</template>
              </el-table-column>
              <el-table-column label="" align="center">
                <template slot-scope="scope">
                  <el-button
                    type="danger"
                    icon="el-icon-delete"
                    size="mini"
                    @click="delete_cart(scope.row)"
                  ></el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
          <!--  -->
          <div class="account">
            <el-col :span="15">
              <el-col :span="2">
                <pre style="margin:0; padding: 0;">&nbsp;</pre>
              </el-col>
              <el-col :span="4">
                <p style="font-size: 1.5em;color: red;">&nbsp;</p>
              </el-col>
            </el-col>
            <el-col :span="4">
              <p style="text-align: right;">
                <span style="font-size: 0.5em;"></span>
                <span style="font-size: 0.5em; color: red;"> &nbsp;</span>
                <span style="font-size: 1.2em; color: red;">{{ total_price }}</span>
              </p>
              <p style="font-size: 0.5em; text-align: right;">
                <span></span>
                <span style="color: red;">&nbsp;{{ count }}&nbsp;</span>
                <span></span>
              </p>
            </el-col>
            <el-col :span="1">
              <pre style="margin:0; padding: 0;">&nbsp;</pre>
            </el-col>
            <el-col :span="4">
              <div class="submit_button">
                <el-link
                  style="color: white; font-size: 1.2em;"
                  :underline="false"
                  @click="to_create_order"
                >&nbsp;&nbsp;&nbsp;</el-link>
              </div>
            </el-col>
          </div>
        </div>
      </el-col>
    </el-row>
  </Container>
</template>
<script>
/* eslint-disable */
import Container from "@/components/Container.vue";

export default {
  name: "Cart",
  data() {
    return {
      // 
      skus: [],

      // 
      total_price: 0,
      count: 0,

      // sku
      selected_skus: []
    };
  },
  mounted() {
    this.get_cart();
  },
  methods: {
    // 
    get_cart() {
      let that = this;
      that.$axios.get("cart/").then(res => {
        that.skus = res.data;
      });
    },

    // 
    handleSelectionChange(rows) {
      this.count = 0;
      this.total_price = 0;
      this.selected_skus = rows;
      rows.forEach(value => {
        this.total_price += value.count * value.price;
        this.count += value.count;
      });
    },

    // handleSelectionChange
    handleRowClick(row) {
      this.$refs.skuList.toggleRowSelection(row, false);
      // this.$refs.skuList.toggleRowSelection(row, true);
    },

    //  async  await 
    edit_cart: function(row) {
      // 
      let request_data = {
        sku_id: row.id,
        count: row.count,
        spec: row.spec
      };

      this.$axios.put("cart/", {
        data: request_data
      });
    },

    // 
    delete_cart(row) {
      this.$axios
        .delete("cart/", {
          data: row
        })
        .then(res => {
          if (res.status == 200) {
            this.get_cart();
          }
        });
    },

    // 
    to_create_order() {
      if (this.selected_skus.length == 0) {
        this.$notify.warning({
          title: ""
        });
        return;
      }
      if (this.$store.state.token == undefined) {
        this.$notify.warning({
          title: ""
        });
        return;
      }

      // 
      let request_data = [];
      this.selected_skus.forEach((value, index) => {
        let sku = {};
        sku.sku_id = value.id;
        sku.spec = value.spec;

        request_data[index] = sku;
      });
      let params = { skus: request_data };

      this.$api.selects(params).then(res => {
        if (res.status == 201) {
          this.$router.push({
            name: "create_order"
          });
        }
      });
    }
  },
  components: {
    Container
  },
  watch: {}
};
</script>
