import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Register from '../views/Register.vue'
import Login from '../views/Login.vue'
import BindUser from '../views/BindUser.vue'
import UserCenter from '../views/UserCenter.vue'
import Detail from '../components/Detail.vue'
import VerifyEmail from '../components/VerifyEmail.vue'
import VerifyOrder from '../components/VerifyOrder.vue'
import Address from '../components/Address.vue'
import Orders from '../components/Orders.vue'
import Skus from '../views/Skus.vue'
import SkuDetail from '../views/SkuDetail.vue'
import Cart from '../views/Cart.vue'
import CreateOrder from '../views/CreateOrder.vue'
import GoodsList from '../views/GoodsList.vue'




Vue.use(VueRouter)

// ElementUIvue-router3.0
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const originalReplace = VueRouter.prototype.replace;
VueRouter.prototype.replace = function replace(location) {
  return originalReplace.call(this, location).catch(err => err);
}

const routes = [
  {
    path: '/register',
    name: 'register',
    component: Register,
    meta: {
      title: '',
    }
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {
      title: '',
    }
  },
  {
    path: '/',
    name: 'home',
    component: Home,
    meta: {
      title: '',
    }
  },
  {
    path: '/skus',
    name: 'skus',
    component: Skus,
    meta: {
      title: '',
    }
  },
  {
    path: '/goods_list',
    name: 'goods_list',
    component: GoodsList,
    meta: {
      title: '',
    }
  },
  {
    path: '/sku_detail',
    name: 'sku_detail',
    component: SkuDetail,
    meta: {
      title: '',
    }
  },
  {
    path: '/cart',
    name: 'cart',
    component: Cart,
    meta: {
      title: '',
    }
  },
  {
    path: '/oauth_callback.html',
    name: 'binduser',
    component: BindUser,
    meta: {
      title: '',
    }
  },
  {
    path: '/verify_order',
    name: 'verify_order',
    component: VerifyOrder,
    meta: {
      title: '',
    }
  },
  {
    path: '/create_order',
    name: 'create_order',
    component: CreateOrder,
    meta: {
      title: '',
      requireAuth: true
    }
  },
  {
    path: '/user_center',
    name: 'user_center',
    component: UserCenter,
    children: [
      {
        path: 'detail', ////;pathpathpath
        name: 'detail',
        component: Detail,
        meta: {
          title: '',
          requireAuth: true
        }
      },
      {
        path: 'address',
        name: 'address',
        component: Address,
        meta: {
          title: '',
          requireAuth: true
        }
      },
      {
        path: 'orders',
        name: 'orders',
        component: Orders,
        meta: {
          title: '',
          requireAuth: true
        }
      },
      {
        path: 'verify_email',
        name: 'verify_email',
        component: VerifyEmail,
      },
    ]

  },
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = new VueRouter({
  mode: 'history', //# 
  routes
})



export default router
