<template>
  <Container>
    <el-row>
      <el-col :span="3">
        <pre style="margin:0; padding: 0;">&nbsp;</pre>
      </el-col>
      <el-col :span="18">
        <!--  -->
        <el-row>
          <p class="row_title">
            <span></span>
          </p>
          <el-divider></el-divider>
          <div class="text_box">
            <div class="address">
              <el-radio-group v-model="address_id">
                <el-radio
                  v-for="address in addresses"
                  :key="address.id"
                  :label="address.id"
                  style="margin-top:0.5em;"
                >
                  {{ address.province }}&nbsp;{{address.city}}&nbsp;{{address.district}}&nbsp;{{address.place}}
                  &nbsp;{{address.receiver}}&nbsp;{{address.mobile}}
                </el-radio>
              </el-radio-group>
            </div>
          </div>
        </el-row>
        <!--  -->
        <el-row>
          <p class="row_title">
            <span></span>
          </p>
          <el-divider></el-divider>
          <div class="text_box">
            <div class="address">
              <el-radio-group v-model="pay_method">
                <el-radio :label="1" style="margin:0 0.5em;"></el-radio>
                <el-radio :label="2" style="margin:0 0.5em;"></el-radio>
                <el-radio :label="3" style="margin:0 0.5em;"></el-radio>
              </el-radio-group>
            </div>
          </div>
        </el-row>
        <!--  -->
        <el-row>
          <p class="row_title">
            <span></span>
          </p>
          <el-divider></el-divider>
          <div>
            <div>
              <el-table
                :data="skus"
                size="mini"
                height="32em"
                :header-cell-style="{background:'#eef1f6',color:'#606266', 'text-align': 'center'}"
                :header-row-style="{height:'3em'}"
              >
                <el-table-column type="index" width="40" align="center"></el-table-column>
                <el-table-column width="150" align="center">
                  <template slot-scope="scope">
                    <el-image :src="scope.row.default_image_url" style="width: 8em; height: 8em;"></el-image>
                  </template>
                </el-table-column>
                <el-table-column prop="name" label="" width="300"></el-table-column>
                <el-table-column prop="spec" label="" width="200" align="center"></el-table-column>
                <el-table-column prop="price" label="" width="120" align="center"></el-table-column>
                <el-table-column label="" width="90" align="center">
                  <template slot-scope="scope">{{ scope.row.count }}</template>
                </el-table-column>
                <el-table-column label="" width="150" align="center">
                  <template
                    slot-scope="scope"
                  >{{ (scope.row.count * scope.row.price).toFixed(2) }} &nbsp;</template>
                </el-table-column>
              </el-table>
            </div>
          </div>
        </el-row>
        <!--  -->
        <el-row>
          <p class="row_title">
            <span></span>
          </p>
          <el-divider></el-divider>
          <div class="text_box">
            <p class="settle">
              <span style="font-size: 0.5em;"></span>
              <span style="font-size: 1em; color: red;">&nbsp;{{ settle.count }}&nbsp;</span>
              <span style="font-size: 0.5em;">&nbsp;</span>
              <span style="font-size: 0.5em;"></span>
              <span style="font-size: 1em; color: red;">{{ settle.goods_price.toFixed(2) }}&nbsp;</span>
            </p>
            <p class="settle">
              <span style="font-size: 0.5em;"></span>
              <span style="font-size: 1em; color: red;">&nbsp;{{ settle.freight.toFixed(2) }}&nbsp;</span>
            </p>
            <p class="settle">
              <span style="font-size: 0.5em;"></span>
              <span
                style="font-size: 1em; color: red;"
              >&nbsp;{{ settle.total_amount.toFixed(2) }}&nbsp;</span>
            </p>
          </div>
          <div>
            <el-col :span="20">
              <pre style="margin:0; padding: 0;">&nbsp;</pre>
            </el-col>
            <el-col :span="4">
              <div class="submit_button">
                <el-link
                  style="color: white; font-size: 1.2em;"
                  :underline="false"
                  @click="submit_order"
                >&nbsp;&nbsp;&nbsp;</el-link>
              </div>
            </el-col>
          </div>
        </el-row>
        <!--  -->
        <el-dialog :visible.sync="dialogVisible" width="40em" :before-close="handleClose">
          <el-row>
            <el-col :span="6">
              <el-image :src="success" style="width: 10em;"></el-image>
            </el-col>
            <el-col :span="18" style="text-align: center;">
              <p class="create_success">
                <span></span>
                <span style="color: red;">{{ order_id }}</span>
              </p>
              <p class="create_success">
                <span></span>
              </p>
              <p class="create_success">
                <span></span>
              </p>
            </el-col>
          </el-row>
          <span slot="footer" class="dialog-footer">
            <el-button @click="$router.replace({name: 'home'})">   </el-button>
            <el-button v-if="pay_method != 1" type="danger" @click="pay_order">   </el-button>
          </span>
        </el-dialog>
      </el-col>
    </el-row>
  </Container>
</template>
<script>
import Container from "@/components/Container.vue";

export default {
  name: "CreateOrder",
  data() {
    return {
      // sku
      skus: [],

      // 
      addresses: [],
      address_id: 0,

      // 
      pay_method: 2,

      // 
      order_id: 0,

      // 
      dialogVisible: false,

      // 
      success: require("../assets/images/success.jpeg")
    };
  },
  mounted() {
    this.get_addrsss();
    this.order_list();
  },
  methods: {
    // 
    get_addrsss() {
      this.$api.address().then(res => {
        this.addresses = res.data.address;
        this.address_id = this.addresses[0].id;
      });
    },
    // 
    order_list() {
      this.$api.order_settlement_list().then(res => {
        this.skus = res.data;
      });
    },

    // 
    submit_order() {
      // 
      let data = {
        address: this.address_id,
        pay_method: this.pay_method,
        total_count: this.settle.count,
        total_amount: this.settle.total_amount,
        freight: this.settle.freight
      };
      this.$api.submit_order(data).then(res => {
        if (res.status == 201) {
          this.order_id = res.data.order_id;
          this.dialogVisible = true;
        }
      });
    },

    // 
    pay_order() {
      if (this.pay_method == 2) {
        this.$api.payment_url(this.order_id).then(res => {
          window.open(res.data.url);
          // location.href = res.data.url;
          this.$router.push({ name: "orders", params: { id: this.order_id } });
        });
      }
      if (this.pay_method == 3) {
        this.$message({
          message: "",
          type: "warning"
        });
      }
    },

    // 
    handleClose() {
      if (this.pay_method != 1) {
        this.$confirm("").then(() => {
          this.$router.replace({ name: "cart" });
        });
      }
    }
  },
  computed: {
    // 
    settle() {
      let count = 0;
      let goods_price = 0;
      this.skus.forEach(sku => {
        count += sku.count;
        goods_price += sku.count * sku.price;
      });
      let freight = (10 * count) / 2;
      return {
        count: count,
        goods_price: goods_price,
        freight: freight,
        total_amount: freight + goods_price
      };
    }
  },
  components: {
    Container
  }
};
</script>
