<template>
  <div>
    <el-row>
      <el-button type="primary" icon="el-icon-plus" size="mini" @click="show_addrForm"></el-button>
      <span class="tip">
        
        <span style="color: red; margin: 0.5em;">{{ count }}</span>
        
        <span style="color: red; margin: 0.5em;">{{ total }}</span>
      </span>
    </el-row>
    <el-divider></el-divider>
    <!--  -->
    <div style="height: 29em; overflow:auto; width: 50em;">
      <el-row v-for="(address, index) in addresses" :key="address.title">
        <div class="detail">
          <el-row style="margin-left: 4em;">
            <el-col :span="22">
              <span v-if="is_set_title[index]">
                <el-input v-model="input_title" placeholder="" size="mini"></el-input>&nbsp;
                <el-button type="primary" @click="edit_title(index)" size="mini"></el-button>
                <el-button type="info" @click="hide_edit_title" size="mini"></el-button>
              </span>
              <span v-else>
                <span style="font-size: 2em;">{{ address.title }}</span>
                <el-button
                  type="text"
                  size="mini"
                  style="margin: 0 2em;"
                  @click="show_edit_title(index)"
                ></el-button>
                <span v-if="address.is_default">
                  <el-tag type="danger" effect="dark" size="mini"></el-tag>
                </span>
              </span>
            </el-col>
            <el-col :span="2" style="text-align: right;">
              <el-link :underline="false" @click="delete_address(index)">
                <i class="el-icon-close"></i>
              </el-link>
            </el-col>
          </el-row>
          <el-row>
            <el-row>
              <el-col :span="3" style="text-align: right;"></el-col>
              <el-col :span="14" style="text-align: left;">{{ address.receiver }}</el-col>
            </el-row>
            <el-row>
              <el-col :span="3" style="text-align: right;"></el-col>
              <el-col
                :span="14"
                style="text-align: left;"
              >{{ address.province }}&nbsp;{{ address.city }}&nbsp;{{ address.district }}</el-col>
            </el-row>
            <el-row>
              <el-col :span="3" style="text-align: right;"></el-col>
              <el-col :span="14" style="text-align: left;">{{ address.place }}</el-col>
            </el-row>
            <el-row>
              <el-col :span="3" style="text-align: right;"></el-col>
              <el-col :span="14" style="text-align: left;">{{ address.mobile }}</el-col>
            </el-row>
            <el-row>
              <el-col :span="3" style="text-align: right;"></el-col>
              <el-col :span="14" style="text-align: left;">{{ address.tel }}</el-col>
            </el-row>
            <el-row>
              <el-col :span="3" style="text-align: right;"></el-col>
              <el-col :span="14" style="text-align: left;">{{ address.email }}</el-col>
            </el-row>
            <el-row>
              <el-col :span="20" style="text-align: right;">
                <el-button @click="edit_address(index)" type="text" size="mini">
                  <i class="el-icon-edit"></i>
                </el-button>
              </el-col>
              <el-col :span="3" style="text-align: right;">
                <el-button @click="set_default_address(index)" type="text" size="mini"></el-button>
              </el-col>
            </el-row>
          </el-row>
        </div>
      </el-row>
    </div>

    <!--  -->
    <el-dialog :title="title" :visible.sync="dialogVisible" width="36em">
      <el-form :model="addrForm" :rules="rules" label-width="8em" ref="addrForm">
        <el-form-item label="" prop="receiver">
          <el-input v-model="addrForm.receiver" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="" required>
          <el-col :span="7">
            <el-form-item prop="province_id">
              <el-select v-model="addrForm.province_id" size="mini">
                <el-option
                  v-for="item in province"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item prop="city_id">
              <el-select v-model="addrForm.city_id" size="mini">
                <el-option v-for="item in city" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item prop="district_id">
              <el-select v-model="addrForm.district_id" size="mini">
                <el-option
                  v-for="item in district"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="place">
          <el-input v-model="addrForm.place" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addrForm.mobile" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="" prop="tel">
          <el-input v-model="addrForm.tel" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addrForm.email" size="mini"></el-input>
        </el-form-item>
      </el-form>
      <span style="margin-left:28em;">
        <el-button @click="reset_addrForm" size="mini"> </el-button>
        <el-button type="primary" @click="submit" size="mini"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "Address",
  data() {
    return {
      // 
      total: 0,
      count: 0,

      // 
      addresses: [],

      // 
      is_set_title: [],
      input_title: "",

      //
      dialogVisible: false,
      title: "",

      // 
      province: [],
      city: [],
      district: [],

      //
      addrForm: {
        receiver: "",
        province_id: "",
        city_id: "",
        district_id: "",
        place: "",
        mobile: "",
        tel: "",
        email: ""
      },

      //   0:  1:
      type: 0,

      //
      rules: {
        receiver: [
          { required: true, message: "", trigger: "blur" }
        ],
        place: [{ required: true, message: "", trigger: "blur" }],
        mobile: [
          {
            required: true,
            pattern: /^1[3-9]{1}\d{9}$/,
            message: "",
            trigger: "blur"
          }
        ],
        province_id: [{ required: true, message: "", trigger: "change" }],
        city_id: [{ required: true, message: "", trigger: "change" }],
        district_id: [{ required: true, message: "", trigger: "change" }]
      }
    };
  },
  mounted() {
    this.get_addrsss();
    this.get_province();
  },

  methods: {
    // 
    get_addrsss() {
      this.$api.address().then(res => {
        this.total = res.data.limit;
        this.addresses = res.data.address;
        this.count = this.addresses.length;
      });
    },
    // 
    show_edit_title(index) {
      this.input_title = this.addresses[index].title;
      for (var i = 0; i < index; i++) {
        this.is_set_title.push(false);
      }
      this.is_set_title.push(true);
    },
    // 
    hide_edit_title() {
      this.is_set_title = [];
    },
    // 
    edit_title(index) {
      let that = this;
      that.$axios
        .put("edit_address_title/", {
          id: that.addresses[index].id,
          title: that.input_title
        })
        .then(res => {
          that.hide_edit_title();
          that.get_addrsss();
          that.$notify({
            type: "success",
            message: res.data.msg
          });
        });
    },
    // 
    delete_address(index) {
      let that = this;
      that.$axios
        .delete("address/", {
          data: {
            id: that.addresses[index].id
          }
        })
        .then(res => {
          that.get_addrsss();
          that.$notify({
            type: "success",
            message: res.data.msg
          });
        });
    },
    // 
    get_province() {
      ";";
      let that = this;
      that.$axios.get("areas/").then(res => {
        that.province = res.data;
      });
    },
    // 
    reset_addrForm() {
      // ,moutedtableDOM
      // prop
      if (this.$refs.addrForm !== undefined) {
        this.$refs.addrForm.resetFields();
      }
    },
    // 
    show_addrForm() {
      this.reset_addrForm();
      this.dialogVisible = true;
      this.type = 0;
      this.title = "";
    },
    // 
    submit() {
      let that = this;
      that.$refs.addrForm.validate(valid => {
        if (valid) {
          if (that.type === 0) {
            that.$axios.post("address/", that.addrForm).then(res => {
              that.$notify({
                type: "success",
                message: res.data.msg
              });
              that.get_addrsss();
              that.dialogVisible = false;
            });
          }
          if (that.type === 1) {
            that.$axios.put("address/", that.addrForm).then(res => {
              that.$notify({
                type: "success",
                message: res.data.msg
              });
              that.get_addrsss();
              that.dialogVisible = false;
            });
          }
        } else {
          return false;
        }
      });
    },
    // 
    edit_address(index) {
      let that = this;
      // 
      that.addrForm.receiver = that.addresses[index].receiver;
      that.addrForm.province_id = that.addresses[index].province_id;
      that.addrForm.city_id = that.addresses[index].city_id;
      that.addrForm.district_id = that.addresses[index].district_id;
      that.addrForm.place = that.addresses[index].place;
      that.addrForm.mobile = that.addresses[index].mobile;
      that.addrForm.tel = that.addresses[index].tel;
      that.addrForm.email = that.addresses[index].email;
      // id
      that.addrForm.id = that.addresses[index].id;
      that.addrForm.title = that.addresses[index].title;

      that.dialogVisible = true;
      that.title = "";
      that.type = 1;
    },
    // 
    set_default_address(index) {
      let that = this;
      if (that.addresses[index].is_default === true) {
        this.$notify({
          type: "warning",
          message: ""
        });
        return;
      }
      that.$axios
        .put("address/", {
          id: that.addresses[index].id,
          is_default: true
        })
        .then(res => {
          that.$notify({
            type: "success",
            message: res.data.msg
          });
          that.get_addrsss();
        });
    }
  },
  // 
  watch: {
    "addrForm.province_id": function() {
      let that = this;
      if (that.addrForm.province_id) {
        that.$axios
          .get("areas/" + that.addrForm.province_id + "/")
          .then(res => {
            that.city = res.data.subs;
            that.district = [];
          })
          .catch(() => {
            that.city = [];
            that.district = [];
          });
      }
    },
    "addrForm.city_id": function() {
      let that = this;
      if (that.addrForm.city_id) {
        that.$axios
          .get("areas/" + that.addrForm.city_id + "/")
          .then(res => {
            that.district = res.data.subs;
          })
          .catch(() => {
            that.district = [];
          });
      }
    }
  }
};
</script>
<style scoped>
.el-divider {
  margin: 0.5em;
  margin-left: 0;
  padding: 0;
}
.tip {
  font-size: 0.5em;
  margin: 2em;
}
.el-row {
  margin: 0.25em;
}
.detail {
  font-size: 0.5em;
  border: 0.5px solid rgb(176, 176, 240);
  margin: 0.5em;
  padding: 0.5em;
  border-radius: 0.5em;
}
.el-input {
  width: 32em;
  margin-right: 1em;
}
.button {
  text-align: right;
  padding-right: 3em;
}
.el-select {
  width: 8em;
}
</style>