<template>
  <div>
    <div v-if="exist" style="text-align: center;">
      <el-image :src="image.loading"></el-image>
    </div>
    <div v-else>
      <el-row>
        <!--  -->
        <el-col :span="14" style="margin-top: 4em; text-align: right;">
          <div>
            <div>
              <el-image :src="image.logo"></el-image>
            </div>
            <div>
              <span class="banner-text"></span>
            </div>
            <div></div>
            <el-image :src="image.register"></el-image>
          </div>
        </el-col>
        <!--  -->
        <el-col :span="1" style="margin-top: 2em; text-align: center;">
          <el-image :src="image.interval"></el-image>
        </el-col>
        <!--  -->
        <el-col :span="9" style="width: 22em; margin-top: 4em;">
          <div>
            <!--  -->
            <el-row style="line-height: 3em;">
              <el-col :span="18">
                <span class="register-text"></span>
              </el-col>
            </el-row>
            <!--  -->
            <el-form size="mini" label-width="6em" :model="form" :rules="rules" ref="registerForm">
              <el-form-item label=":" prop="mobile">
                <el-input v-model="form.mobile"></el-input>
              </el-form-item>
              <el-form-item label=":" prop="password">
                <el-input v-model="form.password" type="password" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item label=":" prop="sms_code">
                <span style="width: 20em;">
                  <el-input style="width: 10em; margin-right:1em;" v-model="form.sms_code"></el-input>
                  <el-button
                    style="width: 8em; text-align: center;"
                    @click="getSmsCode"
                    type="primary"
                    :disabled="send_flag"
                  >{{ send_tips }}</el-button>
                </span>
              </el-form-item>
              <el-form-item style="text-align: center; margin-top: 0px;" label-width="0">
                <el-button type="danger" size="medium" style="width: 20em;" @click="submit">&nbsp;</el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-col>
      </el-row>
      <!--  -->
      <el-divider></el-divider>
      <!-- footer -->
      <Footer></Footer>
    </div>
  </div>
</template>

<script>
import Footer from "@/components/Footer.vue";

export default {
  data() {
    return {
      // 
      image: {
        logo: require("../assets/images/logo.png"),
        register: require("../assets/images/register_banner.png"),
        interval: require("../assets/images/interval_line.png"),
        loading: require("../assets/images/loading.gif")
      },

      // 
      send_flag: false,
      send_tips: "",

      // 
      form: {
        mobile: "",
        password: "",
        sms_code: "",
        openid: ""
      },

      // 
      rules: {
        password: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        mobile: [
          {
            required: true,
            pattern: /^1[3-9]{1}\d{9}$/,
            message: "",
            trigger: "blur"
          }
        ],
        sms_code: [
          {
            required: true,
            pattern: /^[0-9]{6}$/,
            message: "",
            trigger: "blur"
          }
        ]
      },

      // QQ
      exist: true
    };
  },
  mounted() {
    this.checkUser();
  },
  methods: {
    // 
    getSmsCode() {
      let that = this;
      if (that.form.mobile) {
        // 
        if (that.send_flag) {
          return;
        }

        that.$axios.get("sms_codes/" + that.form.mobile, {}).then(() => {
          // 6060
          that.send_flag = true;
          let num = 60;
          // 
          let timer = setInterval(
            () => {
              if (num == 1) {
                // , 
                clearInterval(timer);
                // 
                that.send_tips = "";
                // onclick
                that.send_flag = false;
              } else {
                num -= 1;
                // 
                that.send_tips = num + "";
              }
            },
            1000,
            60
          );
        });
      } else {
        that.$message.error("");
      }
    },
    // QQQQ
    submit() {
      this.$refs.registerForm.validate(valid => {
        if (valid) {
          let that = this;
          that.$axios.post("oauth/qq/user/", that.form).then(res => {
            // 200
            //token,id,username
            that.$router.commit("setStatus", {
              username: res.data.username,
              token: res.data.token
            });

            // 
            that.$router.replace(that.$route.query.redirect || "/");
          });
        }
        return false;
      });
    },
    // 
    getCode(name) {
      let url = window.location.href;

      let params = url.substr(url.lastIndexOf("?") + 1).split("&");
      for (let i = 0; i < params.length; i++) {
        let param = params[i];
        let key = param.split("=")[0];
        let value = param.split("=")[1];
        if (key === name) {
          return value;
        }
      }
    },
    // 
    checkUser() {
      let that = this;

      // stateQQ
      let state = that.getCode("state");

      if (state === "STATE") {
        that.$axios
          .get("oauth/dt/user/", { params: { code: that.getCode("code") } })
          .then(res => {
            if (res.data.openid) {
              // openid
              that.exist = false;
              that.form.openid = res.data.openid;
            } else {
              // 
              sessionStorage.clear(); // sessionStorage
              localStorage.clear(); // 
              sessionStorage.token = res.data.token;
              sessionStorage.username = res.data.username;
              sessionStorage.user_id = res.data.id;

              that.$store.commit("setStatus", {
                username: res.data.username,
                token: res.data.token
              });
              // 
              that.$router.replace(that.$route.query.redirect || "/");
            }
          });
      } else {
        that.$axios
          .get("oauth/qq/user", { params: { code: that.getCode("code") } })
          .then(res => {
            if (res.data.openid) {
              // openidQQ
              that.exist = false;
              that.form.openid = res.data.openid;
            } else {
              // 
              sessionStorage.clear(); // sessionStorage
              localStorage.clear(); // 
              sessionStorage.token = res.data.token;
              sessionStorage.username = res.data.username;
              sessionStorage.user_id = res.data.id;

              that.$store.commit("setStatus", {
                username: res.data.username,
                token: res.data.token
              });
              // 
              that.$router.replace(that.$route.query.redirect || "/");
            }
          });
      }
    }
  },
  components: {
    Footer
  }
};
</script>

<style scoped>
.banner-text {
  color: red;
}
.register-text {
  font-size: 1.5em;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: grey;
}
.el-input {
  width: 20em;
}
.el-form-item {
  padding-bottom: 2em;
}
.el-form {
  background-color: rgb(180, 202, 206);
  border-radius: 5px;
  padding-top: 2em;
}
</style>