<template>
  <div>
    <el-row>
      <el-col :span="21">
        <el-input
          placeholder=""
          size="mini"
          style="width: 25em;"
          v-model="order_id"
          onkeyup="value = value.replace(/[^\d]/g,'');"
        >
          <el-button slot="append" icon="el-icon-search" @click="query_order()"></el-button>
        </el-input>
      </el-col>
      <el-col :span="3" style="color: grey;">
        <span v-if="loading">
          <i class="el-icon-loading" slot="suffix"></i>...
        </span>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <div style="height: 29em; width: 51em;">
      <el-row>
        <el-table
          :data="orders"
          height="39em"
          ref="table"
          size="mini"
          :header-cell-style="{background:'#eef1f6',color:'#606266', 'text-align': 'center'}"
          style="overflow-anchor:none;"
        >
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-table
                :data="props.row.skus"
                size="mini"
                :header-cell-style="{background:'#eef1f6',color:'#606266', 'text-align': 'center'}"
              >
                <el-table-column align="center" width="90">
                  <template slot-scope="scope">
                    <el-image
                      :src="scope.row.sku.default_image_url"
                      style="width: 5em; height: 5em;"
                    ></el-image>
                  </template>
                </el-table-column>
                <el-table-column label="" width="250">
                  <template slot-scope="scope">{{ scope.row.sku.name }}</template>
                </el-table-column>
                <el-table-column align="center" label="" prop="spec" width="120"></el-table-column>
                <el-table-column align="center" label="" prop="count" width="80"></el-table-column>
                <el-table-column align="center" label="" prop="price" width="100"></el-table-column>
                <el-table-column align="center" label="" prop="score" width="80"></el-table-column>
              </el-table>
            </template>
          </el-table-column>
          <el-table-column align="center" label="" width="80" type="index"></el-table-column>
          <el-table-column align="center" label="" prop="order_id" width="160"></el-table-column>
          <el-table-column align="center" label="" prop="total_count" width="80"></el-table-column>
          <el-table-column align="center" label="" prop="total_amount" width="120"></el-table-column>
          <el-table-column align="center" label="" prop="freight" width="120"></el-table-column>
          <el-table-column align="center" label="" prop="status_text" width="85"></el-table-column>
          <el-table-column align="center" label="" width="120">
            <template slot-scope="scope">
              <el-button
                type="primary"
                size="mini"
                v-if="scope.row.status == 1"
                @click="pay_order(scope.row.order_id)"
              ></el-button>
              <el-button type="primary" size="mini" v-if="scope.row.status == 2"></el-button>
              <el-button type="primary" size="mini" v-if="scope.row.status == 3"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-row>
    </div>
  </div>
</template>
<script>
/* eslint-disable */
export default {
  name: "Orders",
  data() {
    return {
      // 
      orders: [],

      // 
      page: 1,
      page_size: 10,
      total_page: 0,

      // 
      loading: false,

      // 
      load: true,

      // 
      order_id: ""
    };
  },
  mounted() {
    if (this.$route.params.id) {
      // undefined
      this.order_id = this.$route.params.id;
    }
    this.query_order();
  },
  methods: {
    // 
    get_orders() {
      this.$api
        .orders({
          page: this.page,
          page_size: this.page_size
        })
        .then(res => {
          this.orders = res.data.results;
          //   
          this.total_page = Math.ceil(res.data.count / this.page_size);

          // scrollHeight: 
          // scrollTop: 
          // clientHeight: 
          // scrollTop + clientHeight = scrollHeight ,
          // dom
          //   let dom = document.querySelector('.el-table__body');
          let dom = this.$refs.table.bodyWrapper;
          let that = this;
          dom.addEventListener("scroll", function() {
            const scrollDistance =
              dom.scrollHeight - dom.scrollTop - dom.clientHeight;
            // 
            if (scrollDistance <= 0 && that.load) {
              //0
              if (that.page < that.total_page) {
                //
                that.page++; //
                that.loading = true; // 
                that.load = false;
                //
                that.$api
                  .orders({
                    page: that.page,
                    page_size: that.page_size
                  })
                  .then(res => {
                    //
                    that.orders = that.orders.concat(res.data.results);
                    that.loading = false;
                    that.load = true;
                  })
                  .catch(() => {
                    that.loading = false;
                    that.load = true;
                  });
              }
            }
          });
        });
    },

    // 
    get_order(order_id) {
      this.loading = true; // 
      this.load = false;
      this.$api
        .order(order_id)
        .then(res => {
          // push
          this.orders.push(res.data);
          this.loading = false;
          this.load = true;
        })
        .catch(() => {
          this.loading = false;
          this.load = true;
        });
    },

    // 
    query_order() {
      this.orders = []; // 
      this.page = 1; // page
      if (this.order_id) {
        this.get_order(this.order_id);
      } else {
        this.get_orders();
      }
    },

    // 
    pay_order(order_id) {
      this.$api.payment_url(order_id).then(res => {
        window.open(res.data.url);
        // location.href = res.data.url;
      });
    }
  }
};
</script>
<style scoped >
.el-divider {
  margin: 0.5em;
  margin-left: 0;
  padding: 0.1em 0;
  background-color: tomato;
}
div /deep/.el-input__inner {
  border: 2px solid #e23b3b;
}
</style>