import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import api from './api'

import VueWechatTitle from "vue-wechat-title";
import ElementUI from 'element-ui';
import { MessageBox, Message } from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css';

import axios from 'axios'


// 
if (process.env.NODE_ENV === 'development') {
  //  Django 
  axios.defaults.baseURL = 'http://127.0.0.1:8000/';
} else {
  //  nginx 
  axios.defaults.baseURL = '/api/';
}

// cookiecookie
axios.defaults.withCredentials = true

// http request 
axios.interceptors.request.use(
    config => {
        if (store.state.token) {  // tokenhttp headertokenJWT
            config.headers.Authorization = `JWT ${store.state.token}`;
        }
        return config;
    },
    err => {
        return Promise.reject(err);
    });

// http response 
axios.interceptors.response.use(
    response => {
        return response;
    },
    error => {
        let res = error.response
        if (res) {
            switch (res.status) {
                case 401:
                    MessageBox.confirm(
                        '',
                        '',
                        {
                            confirmButtonText: '',
                            cancelButtonText: '',
                            type: 'warning'
                        }
                    )
                        .then(() => {
                            //  401 token
                            store.commit('setStatus');
                            router.replace({
                                name: 'login',
                                query: { redirect: router.currentRoute.fullPath }  // 
                            });
                            return;
                        })
                        .catch(() => {
                            store.commit('setStatus');
                            router.go(0);  // 
                            return;
                        })
                    break;
                case 400:
                    Message({
                        message: res.data.msg || '',
                        type: 'error',
                    })
                    break;

                case 403:
                    Message({
                        message: res.data.msg || '',
                        type: 'error',
                    })
                    break;

                case 404:
                    Message({
                        message: res.data.msg || '',
                        type: 'error',
                    })
                    break;

                case 500:
                    Message({
                        message: res.data.msg || '',
                        type: 'error',
                    })
                    break;

                case 501:
                    Message({
                        message: res.data.msg || '',
                        type: 'warning',
                    })
                    break;

                default:
                    Message({
                        message: res.data.msg || '...',
                        type: 'error',
                    })
            }
        }
        // return Promise.reject(error.response.data)   // 
        return Promise.reject(error.response)   // 

    });

Vue.prototype.$axios = axios;    //:this.$axios
Vue.prototype.$api = api;    //:this.$api  api


//  
router.beforeEach((to, from, next) => {
    if (to.meta.requireAuth) {  // 
        if (store.state.token) {  // vuex statetoken
            next();
        }
        else {
            
            next({
                name: 'login',
                query: { redirect: to.fullPath }  // path
            })
        }
    }
    else {
        next();
    }
})

Vue.config.productionTip = false

Vue.use(ElementUI);
Vue.use(VueWechatTitle);


new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')
