<template>
  <div>
    <el-row>
      <!--  -->
      <el-col :span="14" style="margin-top: 4em; text-align: right;">
        <div>
          <div>
            <el-image :src="image.logo"></el-image>
          </div>
          <div>
            <span class="banner-text"></span>
          </div>
          <div></div>
          <el-image :src="image.register"></el-image>
        </div>
      </el-col>
      <!--  -->
      <el-col :span="1" style="margin-top: 2em; text-align: center;">
        <el-image :src="image.interval"></el-image>
      </el-col>
      <!--  -->
      <el-col :span="9" style="width: 22em; margin-top: 4em;">
        <div>
          <!--  -->
          <el-row style="line-height: 3em;">
            <el-col :span="18">
              <span class="register-text"></span>
            </el-col>
            <el-col :span="6" style="text-align: right;">
              <!-- clickrouter -->
              <el-button type="text" @click="$router.push({ name: 'login' })">
                
                <i class="el-icon-d-arrow-right el-icon--right"></i>
              </el-button>
            </el-col>
          </el-row>
          <el-divider></el-divider>

          <!--  -->
          <el-form size="mini" label-width="6em" :model="form" :rules="rules" ref="registerForm">
            <el-form-item label=":" prop="username">
              <el-input v-model="form.username"></el-input>
            </el-form-item>
            <el-form-item label=":" prop="password">
              <el-input v-model="form.password" type="password" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label=":" prop="password2">
              <el-input
                v-model="form.password2"
                type="password"
                auto-complete="off"
                ref="password2"
              ></el-input>
            </el-form-item>
            <el-form-item label=":" prop="mobile">
              <el-input v-model="form.mobile"></el-input>
            </el-form-item>
            <el-form-item label=":" prop="sms_code">
              <span style="width: 20em;">
                <el-input style="width: 10em; margin-right:1em;" v-model="form.sms_code"></el-input>
                <el-button
                  style="width: 8em; text-align: center;"
                  @click="getSmsCode"
                  type="primary"
                  :disabled="send_flag"
                >{{ send_tips }}</el-button>
              </span>
            </el-form-item>
            <el-form-item style="text-align: left;" label-width="6em" prop="allow">
              <el-checkbox v-model="form.allow" true-label="true"> </el-checkbox>
            </el-form-item>
            <el-form-item style="text-align: center; margin-top: 0px;" label-width="0">
              <el-button type="danger" size="medium" style="width: 20em;" @click="submit"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-col>
    </el-row>
    <!-- footer -->
    <Footer></Footer>
  </div>
</template>

<script>
import Footer from "@/components/Footer.vue";

export default {
  name: "Register",
  data() {
    // callback()
    const check_pwd = (rule, value, callback) => {
      if (this.form.password === value) {
        callback();
      } else {
        this.form.password2 = "";
        // 
        // this.$refs.password2.focus();
        callback(new Error(""));
      }
    };
    const check_username = (rule, value, callback) => {
      this.$axios
        .get("users/" + value + "/count/")
        .then(res => {
          if (res.data.count > 0) {
            callback(new Error(""));
          }
          callback();
        })
        .catch(err => {
          this.$message.error(err.message);
        });
    };
    const check_mobile = (rule, value, callback) => {
      this.$axios
        .get("users/" + value + "/count/")
        .then(res => {
          if (res.data.count > 0) {
            callback(new Error(""));
          }
          callback();
        })
        .catch(err => {
          this.$message.error(err.message);
        });
    };

    return {
      // 
      image: {
        logo: require("../assets/images/logo.png"),
        register: require("../assets/images/register_banner.png"),
        interval: require("../assets/images/interval_line.png")
      },

      // 
      send_flag: false,
      send_tips: "",

      // 
      form: {
        username: "",
        password: "",
        password2: "",
        mobile: "",
        sms_code: "",
        allow: ""
      },

      // 
      rules: {
        username: [
          {
            required: true,
            pattern: /^\w{5,20}$/,
            message: "520",
            trigger: "blur"
          },
          { validator: check_username, trigger: "blur" }
        ],
        password: [
          {
            required: true,
            pattern: /^\w{8,20}$/,
            message: "820",
            trigger: "blur"
          }
        ],
        password2: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          { validator: check_pwd, trigger: "blur" }
        ],
        mobile: [
          {
            required: true,
            pattern: /^1[3-9]{1}\d{9}$/,
            message: "",
            trigger: "blur"
          },
          { validator: check_mobile, trigger: "blur" }
        ],
        sms_code: [
          {
            required: true,
            pattern: /^[0-9]{6}$/,
            message: "",
            trigger: "blur"
          }
        ],
        allow: [
          { required: true, message: "", trigger: "change" }
        ]
      }
    };
  },
  mounted() {},
  methods: {
    // 
    getSmsCode() {
      let that = this;
      if (that.form.mobile) {
        // 
        if (that.send_flag) {
          return;
        }

        that.$axios
          .get("sms_codes/" + that.form.mobile + "/")
          .then(res => {
            if (res.status == 200) {
              // 6060
              that.send_flag = true;
              let num = 60;
              // 
              let timer = setInterval(
                () => {
                  if (num == 1) {
                    // , 
                    clearInterval(timer);
                    // 
                    that.send_tips = "";
                    // onclick
                    that.send_flag = false;
                  } else {
                    num -= 1;
                    // 
                    that.send_tips = num + "";
                  }
                },
                1000,
                60
              );
            }
          })
      } else {
        that.$message.error("");
      }
    },
    // 
    submit() {
      this.$refs.registerForm.validate(valid => {
        if (valid) {
          let that = this;
          that.$axios
            .post("users/", that.form)
            .then(res => {
              // 201
              // token
              sessionStorage.clear(); // sessionStorage
              localStorage.clear(); // 

              sessionStorage.token = res.data.token;
              sessionStorage.username = res.data.username;
              sessionStorage.user_id = res.data.id;

              this.$store.commit("setStatus", {
                username: res.data.username,
                token: res.data.token
              });

              // 
              that.$router.replace(that.$route.query.redirect || "/");
            })
            .catch(err => {
              that.$message.error("");
              that.$message.error(err.message);
            });
        } else {
          return false;
        }
      });
    }
  },
  components: {
    Footer
  }
};
</script>

<style scoped>
.banner-text {
  color: red;
}
.register-text {
  font-size: 1.5em;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: grey;
}
.el-input {
  width: 20em;
}
.el-form-item {
  margin-top: 2em;
}
.el-form {
  padding-bottom: 1em;
}
.el-divider {
  margin: 0;
  padding: 0;
}
</style>