<template>
  <div style="width: 50em; margin-top: 2em;">
    <div v-if="loading">
      <el-image :src="image.loading"></el-image>
    </div>
    <div v-if="verify_pass">
      <el-image :src="image.success" style="width: 5em; height: 5em;"></el-image>
      <p style="font-size: 2.5em; color: green;"></p>
      <el-button type="primary" @click="$router.push({name: 'home'})"></el-button>
    </div>
    <div v-if="verify_fail">
      <el-image :src="image.failure" style="width: 5em; height: 5em;"></el-image>
      <p style="font-size: 2.5em; color: red;"></p>
      <p style="font-size: 1em; color: red;">{{ error_message }}</p>
      <el-button type="primary" @click="$router.push({name: 'home'})"></el-button>
    </div>
  </div>
</template>
<script>
export default {
  name: "VerifyEmail",
  data() {
    return {
      // 
      verify_pass: false,
      verify_fail: false,
      error_message: "",

      // 
      loading: true,

      // 
      image: {
        success: require("../assets/images/success.jpeg"),
        failure: require("../assets/images/failure.jpeg"),
        loading: require("../assets/images/loading.gif")
      }
    };
  },
  mounted() {
    this.verify_email();
  },
  methods: {
    // 
    getCode(name) {
      let url = window.location.href;

      let params = url.substr(url.lastIndexOf("?") + 1).split("&");
      for (let i = 0; i < params.length; i++) {
        let param = params[i];
        let key = param.split("=")[0];
        let value = param.split("=")[1];
        if (key === name) {
          return value;
        }
      }
    },
    verify_email() {
      let token = this.getCode("token");
      let that = this;
      that.$axios
        .get("verify_email/", {
          params: {
            token: token
          }
        })
        .then(() => {
          that.loading = false;
          that.verify_pass = true;
        })
        .catch(err => {
          that.loading = false;
          that.verify_fail = true;
          that.error_message = err.response.data.msg;
        });
    }
  }
};
</script>
<style scoped>
p {
  margin: 0.5em;
}

div {
  background-color:white;
  text-align: center;
}
</style>