<template>
  <!-- div -->
  <div style="height: 32em; overflow:auto; width: 50em;">
      <el-row>
        <p style="margin-top: 0;"></p>
        <el-divider></el-divider>
        <div class="info">
          <el-col :span="1">
            <pre style="margin:0; padding: 0;">&nbsp;</pre>
          </el-col>
          <!--  -->
          <el-col :span="2">
            <p></p>
            <p></p>
            <p>Email</p>
          </el-col>
          <el-col :span="14" style="text-align: left;">
            <p>{{ username }}</p>
            <p>{{ mobile }}</p>
            <p>
              <span v-if="has">
                {{ email }}&nbsp;
                <span v-if="email_active">
                  <el-tag type="success" effect="dark" size="mini"></el-tag>
                </span>
                <span v-else>
                  <span style="font-size: 0.5em; color: red;"></span>
                  <el-button size="mini" type="info" @click="bind_email"></el-button>
                </span>
              </span>
              <span v-else>
                <span v-if="show">
                  <el-input v-model="input_email" placeholder="" size="mini"></el-input>&nbsp;
                  <el-button type="primary" @click="bind_email"></el-button>
                  <el-button type="info" @click="show = false"></el-button>
                </span>
                <span v-else>
                  <span style="font-size: 0.5em; color: red;"></span>&nbsp;
                  <el-button type="info" @click="show = true"></el-button>
                </span>
              </span>
            </p>
          </el-col>
        </div>
      </el-row>
      <el-row>
        <p></p>
        <el-divider></el-divider>
        <el-col :span="4" v-for="item in sku_set" :key="item.id" class="detail_box">
          <el-link
            :underline="false"
            @click="$router.push({name: 'sku_detail', query:{sku_id: item.id}})"
          >
            <el-image :src="item.default_image_url"></el-image>
          </el-link>
          <div class="text" :title="item.name">{{ item.name }}</div>
          <div style="color:red;">
            {{ item.price }}
            <span class="comments">{{ item.comments }}</span>
          </div>
        </el-col>
      </el-row>
    </div>
</template>
<script>
/* eslint-disable */
export default {
  name: "Detail",
  data() {
    return {
      // 
      username: "",
      mobile: "",
      email: "",

      // 
      has: false, // 
      email_active: false, // 
      show: false, // 

      //
      input_email: "",

      // 
      sku_set: []
    };
  },
  mounted() {
    this.get_user_info();
    this.get_browsing_history();
  },
  methods: {
    // 
    get_user_info() {
      let that = this;
      that.$axios.get("user/", {}).then(res => {
        // 
        that.username = res.data.username;
        that.mobile = res.data.mobile;
        if (res.data.email) {
          that.email = res.data.email;
          that.has = true;
          if (res.data.email_active === true) {
            that.email_active = true;
          }
        }
      });
    },

    //
    bind_email() {
      let that = this;
      if (that.email) {
        that.input_email = that.email;
      }
      // 
      let re = /^[a-z0-9][\w\.\-]*@[a-z0-9\-]+(\.[a-z]{2,5}){1,2}$/;
      if (re.test(that.input_email)) {
        //
        that.$axios
          .put(
            "email/",
            { email: that.input_email },
            {
              // JWT token
              headers: {
                Authorization: "JWT " + that.$store.state.token // token
              }
            }
          )
          .then(res => {
            // 
            if (that.input_email === res.data.email) {
              that.$notify({
                type: "success",
                title: "",
                message: "5"
              });
              that.email = res.data.email;
              that.has = true;
            }
          });
      } else {
        that.$notify.error({
          title: "",
          message: ""
        });
      }
    },

    // 
    get_browsing_history() {
      let that = this;
      that.$axios.get("set_history/").then(res => {
        that.sku_set = res.data;
      });
    }
  }
};
</script>
